body {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 19px;
  color: #222126;
}

* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul,
p {
  margin: 0;
  padding: 0;
}

.section-title {
  font-weight: 600;
  font-size: 64px;
  line-height: 70px;
  text-transform: uppercase;
}
.section-desc {
  font-weight: 400;
  font-size: 20px;
  line-height: 37px;
  margin-top: 32px;
}

.container {
  width: 1312px;
  margin: auto;
  padding: 0px 15px;
  @media (max-width: 1313px) {
    width: 1140px;
  }
  @media (max-width: 1140px) {
    width: 960px;
  }
  @media (max-width: 960px) {
    width: 720px;
  }
  @media (max-width: 720px) {
    width: 540px;
  }
  @media (max-width: 540px) {
    width: 100%;
  }
}

.body-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;

  &__main {
    flex-grow: 1;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

.link-hover {
  position: relative;
  &:after {
    content: "";
    width: 0%;
    height: 1px;
    background-color: #222126;
    display: block;
    position: absolute;
    bottom: 0px;
    transition: width 0.3s ease;
  }
  &:hover {
    &:after {
      width: 100%;
    }
  }
}

.site-button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: $primary-color;
  padding: 15px 24px;
  color: #ffffff;
  cursor: pointer;
  transition: background 0.3s ease;
  border: none;
  font-weight: 600;
  border-radius: 100px;
  box-shadow: 0px 0px 40px rgba(0, 55, 159, 0.4);
  &:hover {
    background: $primary-light-color;
  }
  &--uppercase {
    text-transform: uppercase;
  }
}
